@import url("https://fonts.googleapis.com/css?family=Fira+Sans+Condensed:300,400,500,600&display=swap");

html,
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

textarea:focus,
input:focus {
  outline: none;
}

